"use client";

import Link from "next/link";
import Image from "next/image";
import { useState, useRef } from "react";
import { Menu, X } from "lucide-react";

export default function HorizontalScrollPage() {
  const [isOpen, setIsOpen] = useState(false);
  const scrollContainerRef = useRef(null);

  const scrollLeft = () => {
    if (scrollContainerRef.current) {
      scrollContainerRef.current.scrollBy({
        left: -window.innerWidth,
        behavior: "smooth",
      });
    }
  };

  const scrollRight = () => {
    if (scrollContainerRef.current) {
      scrollContainerRef.current.scrollBy({
        left: window.innerWidth,
        behavior: "smooth",
      });
    }
  };

  return (
    <>
      {/* Navbar */}
      <nav className="w-full fixed top-0 left-0 z-50 shadow-md bg-white bg-opacity-80 backdrop-blur-md">
        <div className="w-full px-6 py-4 flex justify-between items-center">
          <Link href="/" className="flex items-center space-x-3" aria-label="Go to home page">
            <Image src="/Muha.png" alt="MyWebsite Logo" width={40} height={40} className="rounded-full" />
            <span className="text-2xl font-bold text-blue-600 tracking-wide">MyWebsite</span>
          </Link>

          {/* Desktop Menu */}
          <div className="hidden md:flex space-x-8 text-lg font-medium">
            <a href="#section1" className="text-gray-700 hover:text-blue-600 transition">หน้าแรก</a>
            <a href="#section2" className="text-gray-700 hover:text-blue-600 transition">เกี่ยวกับเรา</a>
            <a href="/contact" className="text-gray-700 hover:text-blue-600 transition">ติดต่อ</a>
            <a href="https://github.com/peachpeach06/frontend" target="_blank" rel="noopener noreferrer" className="text-gray-700 hover:text-blue-600 transition">GitHub</a>
          </div>

          {/* Mobile Menu Toggle */}
          <div className="md:hidden">
            <button onClick={() => setIsOpen(!isOpen)} className="text-gray-700 focus:outline-none" aria-label={isOpen ? "Close menu" : "Open menu"} aria-expanded={isOpen}>
              {isOpen ? <X className="w-7 h-7" /> : <Menu className="w-7 h-7" />}
            </button>
          </div>
        </div>

        {isOpen && (
          <div className="md:hidden bg-white bg-opacity-95 px-6 py-4 space-y-3 text-base font-medium shadow-lg rounded-b-lg">
            <a href="/" className="block text-gray-700 hover:text-blue-600 transition" onClick={() => setIsOpen(false)}>หน้าแรก</a>
            <a href="#section2" className="block text-gray-700 hover:text-blue-600 transition" onClick={() => setIsOpen(false)}>เกี่ยวกับเรา</a>
            <a href="/contact" className="block text-gray-700 hover:text-blue-600 transition" onClick={() => setIsOpen(false)}>ติดต่อ</a>
            <a href="https://github.com/peachpeach06/frontend" target="_blank" rel="noopener noreferrer" className="block text-gray-700 hover:text-blue-600 transition" onClick={() => setIsOpen(false)}>GitHub</a>
          </div>
        )}
      </nav>

      {/* Scroll Buttons */}
      <button onClick={scrollLeft} aria-label="Scroll Left" className="fixed top-1/2 left-4 -translate-y-1/2 z-50 bg-white bg-opacity-70 hover:bg-opacity-90 text-gray-700 rounded-full p-3 shadow-lg">◀</button>
      <button onClick={scrollRight} aria-label="Scroll Right" className="fixed top-1/2 right-4 -translate-y-1/2 z-50 bg-white bg-opacity-70 hover:bg-opacity-90 text-gray-700 rounded-full p-3 shadow-lg">▶</button>

      {/* Common Full Background Image */}
      <div className="fixed inset-0 -z-10">
        <Image
          src="/Mutu.jpg" // ใช้ภาพเดียว
          alt="Background Image"
          fill
          style={{ objectFit: "cover" }}
          priority
        />
      </div>

      {/* Horizontal Scroll Sections */}
      <div
        ref={scrollContainerRef}
        className="flex overflow-x-auto scroll-smooth snap-x snap-mandatory"
        style={{ height: "100vh", scrollBehavior: "smooth" }}
      >
        {/* Section 1 */}
        <section id="section1" className="snap-start flex-shrink-0 w-screen h-screen relative">
          <div className="relative z-10 pt-40 text-center text-white px-4">
            <h1 className="text-5xl font-bold drop-shadow-md">ยินดีต้อนรับ!</h1>
            <p className="mt-4 text-lg drop-shadow-sm">นี่คือพื้นหลังแบบเดียวกันทั้งหน้า</p>
          </div>
        </section>

        {/* Section 2 */}
        <section id="section2" className="snap-start flex-shrink-0 w-screen h-screen relative">
          <div className="relative z-10 flex items-center justify-center h-full">
            <h2 className="text-4xl text-white font-bold drop-shadow-md">Section 2</h2>
          </div>
        </section>

        {/* Section 3 */}
        <section id="section3" className="snap-start flex-shrink-0 w-screen h-screen relative">
          <div className="relative z-10 flex flex-col justify-end h-full pb-12 px-4">
            <div className="flex justify-center gap-6 overflow-x-auto">
              {/* Card 1 */}
              <div className="bg-white bg-opacity-90 rounded-xl shadow-lg p-6 w-64">
                <h2 className="text-xl font-bold mb-2 text-blue-600">การ์ดที่ 1</h2>
                <p className="text-gray-700">เนื้อหาหรือคำอธิบายเกี่ยวกับการ์ดนี้</p>
              </div>

              {/* Card 2 */}
              <div className="bg-white bg-opacity-90 rounded-xl shadow-lg p-6 w-64">
                <h2 className="text-xl font-bold mb-2 text-green-600">การ์ดที่ 2</h2>
                <p className="text-gray-700">อธิบายสิ่งสำคัญของเนื้อหาในหน้านี้</p>
              </div>

              {/* Card 3 */}
              <div className="bg-white bg-opacity-90 rounded-xl shadow-lg p-6 w-64">
                <h2 className="text-xl font-bold mb-2 text-purple-600">การ์ดที่ 3</h2>
                <p className="text-gray-700">ข้อความเพิ่มเติมเกี่ยวกับการ์ดสุดท้าย</p>
              </div>
            </div>
          </div>
        </section>
      </div>
    </>
  );
}
